<template>
  <div id="container" class="noSelect" >

    <div id="logo">
    </div>

    <div id="altitude">
      <p>Our current altitude:</p>
      <h1>684m</h1>
    </div>

    <div class="gap"></div>

    <div id="summit">
      <p>Just 781 more ladders to the summit!</p>
    </div>

    <div class="gap"></div>

    <div id="milestone">
      <p>We've 42 ladders until</p>
      <h2>Ben Nevis</h2>
      <p>The UK's highest mountain!</p>
    </div>

    <div class="gap"></div>

    <div id="timer">
      <h2>3 30 16</h2>
    </div>

  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .noSelect {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently supported by Chrome and Opera */
  }

  .gap {
    clear: both;
    padding: 1em;
  }

  h1 {
    padding: 15px;
    margin: -40px 0 -40px 0;
    font-family: 'Baloo Thambi', cursive;
    font-size: 500%;
  }

  h2 {
    padding: 0;
    margin: 0;
    font-family: 'Baloo Thambi', cursive;
  }

  p {
    padding: 0 20px 0 20px;
    margin: 0;
    font-family: 'Jaldi', sans-serif;
    font-size: 200%;
  }

  #logo {
    float: left;
    margin: 15px;
    width: 220px;
    height: 92px;
    background-repeat: no-repeat;
    background-image: url(logo-inverse.png);
  }

  #container {
    background: #2BC0E4; /* fallback for old browsers */
    background: -webkit-linear-gradient(to bottom, #EAECC6, #2BC0E4); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to bottom, #EAECC6, #2BC0E4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    padding: 0;
    margin: 0;
    height: 100vh;
    background-repeat: no-repeat;
    background-size: contain;
    cursor: none;
  }

  #altitude {
    float: right;
    display: inline-block;
    padding: 0;
    margin: 15px 0px 0px 0px;
    background-color: white
  }

  #summit {
    float: right;
    display: inline-block;
    padding: 0;
    margin: 0;
    background-color: white
  }

  #milestone {
    float: right;
    display: inline-block;
    padding: 0;
    margin: 0;
    background-color: white
  }

  #timer {
    float: right;
    display: inline-block;
    padding: 0;
    margin: 0;
    background-color: white
  }
</style>



